  font-weight: 700;
  font-style: normal; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  height: 100vh;
  overflow: auto;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 300;
  line-height: 1.5;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 17px;
  color: #484848; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

p {
  margin: 0;
  padding: 0; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  color: inherit;
  text-decoration: none; }

img {
  max-width: 100%; }

button {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

h1, h2, h3 {
  color: #1fc0fc; }

.button {
  text-transform: uppercase;
  padding: 15px 40px;
  border: 1px solid transparent;
  border-radius: 30px;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  text-align: center;
  background: linear-gradient(146.13deg, #ffc341 20.46%, #ffd701 65.31%);
  color: #ffffff; }

.page-header {
  position: relative;
  width: 100%;
  height: auto;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 20px;
  z-index: 10; }

.main-navigation {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: row; }

.main-navigation__item {
  width: 50%;
  display: flex;
  align-items: stretch; }

.main-navigation__item:nth-child(2n) > * {
  margin-left: auto; }

.page-header__phone {
  height: 18px; }

.page-header__phone-number {
  display: block;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 20px; }

.page-header__logo {
  position: absolute;
  top: 20px;
  left: calc(50% - 68px);
  width: 137px;
  height: auto; }

.page-header__order {
  background: none; }

.page-header__order-btn {
  display: block;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 16px;
  font-variant: small-caps;
  background: none;
  border: none;
  border-bottom: 2px dashed #484848;
  cursor: pointer; }

@media (max-width: 1023px) {
  .page-header {
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 20px; } }

@media (max-width: 767px) {
  .main-navigation {
    width: 50%;
    flex-direction: column;
    margin-left: auto; }
  .main-navigation__item {
    width: 100%;
    margin-left: auto; }
  .main-navigation__item > * {
    margin-left: auto; }
  .page-header__logo {
    position: absolute;
    top: 20px;
    left: 5%;
    width: 137px;
    height: auto; } }

.main-screen__container {
  position: relative;
  width: 100%;
  height: auto;
  margin-top: -40px;
  z-index: 0; }

.main-screen__image-big {
  width: 100%;
  height: auto; }

.main-screen__image-small {
  display: none;
  width: 100%;
  height: auto; }

.main-screen__title {
  position: absolute;
  width: 343px;
  height: 58px;
  top: 50%;
  left: calc(50% - 172px);
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #ffffff; }

.main-screen__scroll {
  position: absolute;
  width: 76px;
  height: 132px;
  top: 60%;
  left: calc(50% - 38px);
  z-index: 1000; }

.main-screen__scroll:hover {
  opacity: 0.5; }

.main-screen__scroll:active {
  outline: none; }

@media (max-width: 767px) {
  .main-screen__image-big {
    display: none; }
  .main-screen__image-small {
    display: block; } }

.about__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 49px;
  text-align: center;
  letter-spacing: -0.02em;
  text-transform: uppercase; }

.about__text {
  width: 52.7%;
  height: auto;
  margin: 0 auto;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 170%;
  text-align: center;
  color: #484848; }

.about__video-container {
  position: relative;
  width: 63.7%;
  height: auto;
  margin: 0 auto; }

.about__video-pic {
  width: 100%;
  height: auto; }

.small-screen {
  display: none; }

.about__play {
  position: absolute;
  top: calc(50% - 48px);
  left: calc(50% - 48px); }

.about__play-btn {
  background: transparent;
  border: none; }

@media (max-width: 1023px) {
  .about__title {
    font-size: 32px; }
  .about__text {
    font-size: 15px; } }

@media (max-width: 767px) {
  .small-screen {
    display: block; }
  .big-screen {
    display: none; } }

.modal-order {
  width: 480px;
  min-height: 520px;
  padding-left: 48px;
  padding-top: 32px;
  padding-right: 32px;
  background-color: lightgrey; }

.modal-order__close-btn {
  display: block;
  font-size: 0;
  width: 18px;
  height: 18px;
  margin-left: auto;
  background-color: transparent;
  background-image: url("../img/btn-close-18-17.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border: none;
  cursor: pointer; }

.modal-order__close-btn:hover {
  opacity: 0.4; }

.modal-order__close-btn:active {
  outline: none; }

.modal-order__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: -0.02em;
  margin-bottom: 16px; }

.modal-order__text {
  padding-right: 48px;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 170%;
  margin-bottom: 32px; }

.modal-order__form-field {
  margin-bottom: 8px; }

.modal-order__form-field:last-child {
  margin-bottom: 24px; }

.modal-order__form-input {
  width: 360px;
  height: 60px;
  padding-left: 30px;
  border: 2px solid #E3E3E3;
  border-radius: 30px; }

.modal-order__form-input::-webkit-input-placeholder {
  /*WebKitbrowsers*/
  font-style: normal;
  text-transform: uppercase;
  color: #484848;
  opacity: 0.5; }

.modal-order__form-input:-moz-placeholder {
  /*MozillaFirefox4to18*/
  font-weight: 500;
  text-transform: uppercase;
  color: #484848;
  opacity: 0.5; }

.modal-order__form-input::-moz-placeholder {
  /*MozillaFirefox19+*/
  font-size: 17px;
  text-transform: uppercase;
  color: #484848;
  opacity: 0.5; }

.modal-order__form-input:-ms-input-placeholder {
  /*InternetExplorer10+*/
  line-height: 20px;
  text-transform: uppercase;
  color: #484848;
  opacity: 0.5; }

.modal-order__wrapper {
  display: flex;
  flex-direction: column; }

.modal-order__btn {
  width: 264px;
  min-height: 59px;
  margin-left: 40px;
  margin-bottom: 16px;
  order: 1; }

.agreement__wrapper {
  order: 2; }

.agreement__label {
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 150%;
  opacity: 0.74;
  padding-left: 50px;
  padding-right: 100px;
  background-image: url("../img/checkbox.png");
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: 0 10px; }
